<template>
  <div class="header">
    <h1>Create a Blog</h1>
  </div>

  <form @submit.prevent="handleSubmit">
    <div class="error-box">
      <div class="error" v-for="err in errors" :key="err">
        {{ err }}
      </div>
    </div>

    <label>Blog title:</label>
    <input
      type="text"
      placeholder="Enter a catchy title here"
      v-model="title"
    />
    <label>Content:</label>
    <textarea cols="90" rows="10" v-model="content"></textarea>
    <label>Your name:</label>
    <input
      type="text"
      placeholder="Please use the same name in all your blogs"
      v-model="author"
    />
    <button>Submit Form</button>
  </form>
  <p>{{ title }}</p>
  <p>{{ content }}</p>
  <p>{{ author }}</p>
</template>

<script>
  export default {
    methods: {
      handleSubmit() {
        //validation
        this.errors = []
        if (this.title.length < 6) this.errors.push(this.titleErr)

        if (!this.content) this.errors.push(this.contentErr)

        if (!this.author) this.errors.push(this.authorErr)

        if (this.errors.length > 0) return //console.log('Failed')
        //else console.log('Passed all tests :)')
        //submit
        const data = {
          title: this.title,
          content: this.content,
          author: this.author,
        }
        fetch(this.backendURL + '/blogs', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': 'no-cors',
          },
          body: JSON.stringify(data),
        }).catch(err => console.log(err))
      },
    },

    data() {
      return {
        title: '',
        content: '',
        author: '',
        backendURL: 'https://blogger-backend-node.herokuapp.com',
        //backendURL: 'http://localhost:5000',
        errors: [],
        titleErr: 'Title is too short...',
        contentErr: 'Content cannot be blank',
        authorErr: 'Please enter your name',
      }
    },
  }
</script>

<style>
  .header {
    padding: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header h1 {
    display: inline-block;
  }

  form {
    padding: 10px 40px;
    display: block;
    margin: 10px auto;
  }

  form label {
    display: block;
    font-size: 26px;
    margin: 5px 0;
  }

  form input {
    font-size: 22px;
    margin: 5px 0;
    padding: 5px;
    border-radius: 5px;
    width: 450px;
  }

  form textarea {
    font-size: 22px;
    margin: 5px 0;
    padding: 5px;
    border-radius: 5px;
  }

  form button {
    display: block;
    font-size: 26px;
    padding: 10px 15px;
    border-radius: 10px;
    margin: 20px auto;
    border: none;
    color: #fff;
    background: crimson;
    cursor: pointer;
  }

  .error {
    font-size: 22px;
    background: crimson;
    display: inline-block;
    margin: 4px 0;
    padding: 5px 10px;
    border-radius: 10px;
    color: #eee;
  }

  .error-box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0;
  }
</style>
