<template>
  <div @click="goToBlogDetails" class="blog-container">
    <h2>{{ blog.title }}</h2>
    <p>by {{ blog.author }}</p>
  </div>
</template>

<script>
  export default {
    props: ['blog'],
    methods: {
      goToBlogDetails() {
        this.$router.push('/blogs/' + this.blog._id)
      },
    },
  }
</script>

<style>
  .blog-container {
    background: #fff;
    margin: 15px 60px;
    padding: 5px 25px;
    border-radius: 7px;
    cursor: pointer;
  }

  .blog-container p {
    font-size: 18px;
    margin-left: 20px;
  }
</style>
