<template>
  <div v-if="blog != null" class="blog">
    <h1>{{ blog.title }}</h1>
    <h3>{{ blog.content }}</h3>
    <h4>-{{ blog.author }}</h4>
    <div class="options">
      <button class="back" @click="goBack">Back</button>
      <button class="delete" @click="deleteBlog">Delete</button>
    </div>
  </div>
  <div v-else class="loading">Loading Blog...</div>
</template>

<script>
  export default {
    data() {
      return {
        blog: null,
        backendURL: 'https://blogger-backend-node.herokuapp.com',
        id: this.$route.params.id,
      }
    },
    mounted() {
      fetch(this.backendURL + '/blogs/' + this.id)
        .then(res => res.json())
        .then(data => (this.blog = data))
        .catch(errr => console.log(err))
    },
    methods: {
      goBack() {
        this.$router.go(-1)
      },
      deleteBlog() {
        fetch(this.backendURL + '/blogs/' + this.id, {
          method: 'DELETE',
        })
          .then(() => this.$router.push('/'))
          .catch(err => console.log(err))
      },
    },
  }
</script>

<style>
  .blog {
    background: #fff;
    margin: 30px;
    border-radius: 10px;
    padding: 10px 25px;
  }

  .blog h1 {
    text-align: center;
    font-size: 36px;
    margin-top: 10px;
  }

  .blog h3 {
    font-size: 28px;
    font-weight: 300;
  }

  .blog h4 {
    font-size: 22px;
    font-weight: 300;
  }
  .loading {
    font-size: 36px;
    font-weight: 300;
    text-align: center;
    margin-top: 100px;
  }
  .options .back {
    border: 2px solid #eee;
    border-radius: 5px;
    background: #fff;
    font-size: 22px;
    padding: 3px 10px;
    margin: 5px 20px;
  }
  .options .back:hover {
    background: #eee;
  }
  .options .delete {
    border: 2px solid #ffc4c4;
    border-radius: 5px;
    background: #fff;
    font-size: 22px;
    padding: 3px 10px;
  }
  .options .delete:hover {
    background: #ffc4c4;
  }
</style>
