<template>
  <div class="header">
    <h1>Latest blogs</h1>
  </div>

  <div class="blogs" v-if="blogs.length">
    <Blog v-for="(blog, index) in blogs" :key="index" :blog="blog" />
  </div>

  <div v-else class="loading">
    <h3>Loading blogs...</h3>
  </div>
</template>

<script>
  import Blog from '../components/Blog'
  export default {
    name: 'Home',
    components: { Blog },
    data() {
      return {
        blogs: [],
        backendURL: 'https://blogger-backend-node.herokuapp.com',
      }
    },
    mounted() {
      fetch(this.backendURL + '/blogs')
        .then(res => res.json())
        .then(data => (this.blogs = data))
        .catch(err => console.log(err))
    },
  }
</script>

<style>
  .header {
    padding: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header h1 {
    display: inline-block;
  }

  .loading {
    text-align: center;
    font-size: 32px;
    font-weight: 200;
    color: #aaa;
  }
</style>
